import React from 'react'
import { AiFillEnvironment ,  AiFillHeart , AiOutlineArrowsAlt , AiFillHome   } from 'react-icons/ai'
const HotelInfo = (props) => {
    return (
        <div className='hotelInfo mt-4 p-4'>
            <div className='container'>
                <div className='hotelInfoContent'>
                    <div className='hotelTitle d-flex justify-content-between align-items-center'>
                        <h1>{props?.hotel?.name}</h1>
                        <AiFillHeart className='text-danger'  onClick={props.onClick} />
                    </div>
                    <div className='d-flex mt-2 '>
                        <div className='bullets d-flex justify-content-start align-items-center me-2'>
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                        <p className='mb-0'>{props.review}</p>

                    </div>
                    <div className='HotelLocation mt-3 d-flex justify-content-start align-items-center'><AiFillEnvironment className='text-dark me-2'  /> <span>
                        {props.hotel?.location?.address?.firstAddressLine}</span></div>
                    <div className='HotelSite mt-3 d-flex justify-content-start align-items-center'><AiFillHome className='text-dark me-2' /> <p className='me-2 mb-0'>Visit Hotel Web Site</p><AiOutlineArrowsAlt  /></div>
                </div>
            </div>
        </div>
    )
}

export default HotelInfo